.hero {
  justify-content: center;
  padding-top: 51px;
  padding-bottom: 64px;
  display: flex;
}

.dish-img {
  object-fit: cover;
  border-radius: 15px;
}

.btn-wrapper {
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  display: flex;
}

.favorites-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 16px;
  width: 100%;
  margin: 0 auto;
  padding: 32px 20px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .favorites-list {
    justify-content: flex-start;
    width: 744px;
  }
}

@media screen and (min-width: 1280px) {
  .favorites-list {
    width: 1120px;
  }
}

.btn-category {
  color: #05050533;
  background-color: var(--primary-white-color);
  cursor: pointer;
  border: 1px solid #05050533;
  border-radius: 15px;
  padding: 14px 18px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-category:hover, .btn-category:focus, .btn-category.is-active {
  color: #f8f8f8;
  background-color: #9bb537;
  border: none;
}

.not-found-wrapper {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.element-icon {
  stroke: none;
  height: 58px;
}

.element-description {
  text-align: center;
  max-width: 335px;
  margin-top: 24px;
  padding-bottom: 64px;
  font-size: 14px;
  line-height: 18px;
}

.is-hidden {
  visibility: hidden;
  display: none;
}

